.filters {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.submitButtonWrapper {
    display: flex;
    justify-content: center;
}

button, input[type=submit] {
    background-color: transparent;
    cursor: pointer;
    text-transform: uppercase;
}

.submit-button {
  margin: 25px 10px 10px 10px;
  /* font-family: 'Univers', sans-serif; */
  font-weight: 600;
  font-size: 20px;
  color: #fff;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .25s;
  border: 3px solid;
  border-color: #e5e5e5;
  border-radius: 5px;
  padding: 7px 15px;
}

.submit-button:hover {
  color: #fbb10a;
  text-shadow: #fbb10a 0 0 10px;
  border-color: #fbb10a;
}

input[type=range] {
  width: 150px;
}

select {
  display:table-row;
  overflow: hidden;
}
option {
  display:table-cell;
}

ul {
  list-style-type: none;
  width: 90%;
  margin: 0 auto;
}

.server {
  border: 3px solid #fff3;
  border-radius: 54px;
  margin-bottom: 10px;
  text-align: start;
  display: flex;
  /* font-family: 'Univers', sans-serif; */
  align-items: flex-start;
}

.server:hover {
  background-color: #ffffff08;
}
  
.server:hover .server-image {
  background-position: right center;
}

.server-image-wrapper {
  height: 102px;
  width: 102px;
  min-height: 102px;
  min-width: 102px;
  margin: 2px;
}

.server-image {
  border-radius: 50px;
  height: 100%;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: cover;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: 0.25s;
  text-align: center;
  /* will-change: transform; */
  display: flex; 
  align-items: center; 
  justify-content: center;
}

.server-image:hover .server-image__map-name {
  opacity: 1;
  backdrop-filter: blur(5px) brightness(0.5);
}

.server-image__map-name {
  cursor: default;
  line-height: 104px;
  width: 100%;
  border-radius: 50px;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: 0.25s;
  text-shadow: 1px 1px 1px #000;
  text-transform: uppercase;
  font-size: calc(8px + 1vmin);
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 800;
  opacity: 0;
}

.server-info {
  margin: 10px 10px;
  width: 60%;
  max-width: 60%;
  /* font-size: calc(6px + 2vmin); */
}

.server-info-server-name {
  cursor: default;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 25px;
}

.server-info-server-params, .server-info-server-mods, .server-started {
  cursor: default;
  font-size: 18px;
  color: #fffc;
}

details>summary {
  cursor: pointer;
}

.server-mods-details-content {
  font-size: 16px;
  background: #000a;
  border: 2px solid #fffa;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
}

.server-utilization {
  cursor: default;
  margin: 10px 10px;
  width: 10%;
  font-size: 25px;
}

progress {
  max-height: 10px;
  width: 90%;
}

.server-location {
  cursor: default;
  margin: 10px 10px;
  width: 25%;
  font-size: 25px;
}



.server-flag-image {
  margin: 0 10px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  /* max-width: 30px; */
}

.uppercase {
  text-transform: uppercase;
}